
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}
a {
  color: #42b983;
}
img {
  max-width: 100%;
  height: auto;
}
.wp-caption {
  max-width: 100%;
}
.aligncenter {
  margin-bottom: 2em;
}
.alignleft {
  float: left;
  margin: 0 2em 0 0;
}
.alignright {
  float: right;
  margin: 0 0 0 2em;
}
.alignleft,
.alignright {
  max-width: 50%;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to {
  opacity: 0
}

